.item__wrapper {
  margin-bottom: 180px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 35px;
}

.img__wrapper {
  width: 50%;
  height: 500px;
  background-color: grey;
  border-radius: 20px;
  flex-shrink: 1;
  margin-top: 10px;
  margin-bottom: 28px;
}

.txt__wrapper {
  width: 50%;
  padding-left: 25px;
}
.item__row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.card__icon {
  padding-top: 30px;
}
.item__title {
  text-transform: uppercase;
  margin-bottom: 7px;
  font-weight: 500;
  font-size: 32px;

  color: #000;
  margin-top: 65px;
  margin-bottom: 35px;
}

