.item__subtitle {
  font-weight: 300;
  font-size: 24px;
  line-height: 1.5;
  color: var(--gray);
  max-width: 500px;
  margin-bottom: 50px;
}
.item__size {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  border-radius: 7px;
  font-weight: 500;
  font-size: 22px;
  line-height: 1.5;
  letter-spacing: 0.01em;
  color: var(--gray);
  width: 100%;
  height: 70px;
  cursor: pointer;
  padding-left: 40px;
  padding-right: 40px;
  margin-bottom: 75px;
  border: 1px #ccc solid;
}
.item__yoursize {
  /* background-color: aquamarine; */
  width: 120px;
  text-align: center;
}
.item__sizetitle {
  color: var(--gray);
  font-weight: 300;
}

.item__sizetitle:hover,
.item__sizetitle:active,
.item__sizetitle:focus {
  color: var(--textColor);
}
.item__btn {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: var(--textColor);
  border-radius: 7px;
  font-weight: 500;
  font-size: 22px;
  line-height: 1.5;
  letter-spacing: 0.01em;
  color: #fff;
  width: 100%;
  height: 70px;
  cursor: pointer;
  padding-left: 40px;
  padding-right: 40px;
  margin-bottom: 30px;
}

.item__btn:hover .item__price,
.item__btn:active .item__price,
.item__btn:focus .item__price {
  color: var(--yellow);
}

.item__btn div {
  display: block;
}

.tabs__txt {
  font-weight: 300;
  font-size: 24px;
  line-height: 1.5;
  color: var(--gray);
  margin-top: 40px;
}

.tabs__txt div {
  display: block;
  margin-bottom: 30px;
}
.tabs__txt span {
  color: #000;
  font-weight: 400;
}
.tabs__title {
  font-weight: 500;
  font-size: 22px;
  line-height: 1.5;
  letter-spacing: 0.01em;
  margin-bottom: 20px;
  cursor: pointer;
  color: var(--gray);
  position: relative;
  transition: all 0.3s ease 0s;
}

.tabs__title:hover,
.tabs__title:focus,
.tabs__title:active {
  color: var(--gray);
}

.tabs__title.active {
  color: black;
}

.tabs__title.active::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -22px;
  width: 100%;
  height: 4px;
  background-color: black;
}

.tabs__content {
  display: none;
  transition: all 0.3s ease 0s;
}

.tabs__content.active {
  display: block;
}

.tabs__nav {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
}
